import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ToastController } from '@ionic/angular';

import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})


export class ShowMessageService {
  alert: any;
  constructor(private toastController: ToastController,
    private alertController: AlertController,
    private router: Router) { }


  errorData: { code: any, message: string }[] = [
    { "code": 400, "message": "Bad Request" },
    { "code": 401, "message": "Unauthorized" },
    { "code": 403, "message": "Forbidden" },
    { "code": 404, "message": "Not Found" },
    { "code": 500, "message": "Internal Server Error" },
    { "code": 502, "message": "Bad Gateway" },
    { "code": 503, "message": "Service Unavailable" },
    { "code": 504, "message": "Gateway Timeout" },
    { "code": 0, "message": "Error" }
  ];

  errorMessage(status: any, errorResp) {
    var errCode = status.toString();
    var errDesc = errorResp.error.message;
    if (errDesc === null || errDesc === undefined) {
      let i = 0;
      while (i < this.errorData.length) {
        if (this.errorData[i].code === status) {
          errDesc = this.errorData[i].message;
        }
        i++;
      }
    }
    if (errDesc) {
      this.noNetworkAlert();
    }
  }

  async noNetworkAlert() {
    if (this.alert != null) {
      this.alert.dismiss();
    }

    this.alert = await this.alertController.create({
      header: 'Alert',
      subHeader: "There seems to be a low network coverage area. Please try again later.",
      cssClass: 'logout-alert',
      buttons: [
        {
          role: 'Done',
          cssClass: 'alert-done',
          text: "Cancel"
        }
      ],
    });

    await this.alert.present();

    const { role } = await this.alert.onDidDismiss();
    if (role == 'Done') {
      this.router.navigate(['login-landing'], { replaceUrl: true });
    }
  }


  async presentToast(msg: any, position?: any) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 800,
      position: position || 'bottom'
    });
    toast.present();
  }


}
